

































import Vue from 'vue';

export default Vue.extend({
  data: () => ({
    item: 1,
    search: '',
    items: [
      { id: 1, title: 'Агломашина 1', icon: 'delete' },
      { id: 2, title: 'Агломашина 2', icon: 'delete' },
      { id: 3, title: 'Агломашина 3', icon: 'delete' },
    ],
    sequence: 3,
  }),
  computed: {
    filteredList() {
      if (this.search) {
        return this.items.filter(shop => (
          shop.title.toLowerCase().includes(this.search.toLowerCase())
        ));
      }
      return this.items;
    },
    canAddShop(): boolean {
      return !!this.search;
    },
  },
  methods: {
    addItem() {
      if (this.search) {
        this.sequence += 1;
        this.items.push({ id: this.sequence, title: this.search, icon: 'delete' });
        this.search = '';
      }
    },
  },
});
